import { Input } from "@mui/material";
import React from "react";
import InputImage2, {prepareFormData} from "./InputImage2";

const ax = window.kcapp.fetchx;

class App extends React.Component {
    constructor(props) {
        super(props);
    }

    onSubmit = (event) => {
        event.preventDefault();
        let form = event.target;

        let formdata = new FormData(form);
        prepareFormData(form, formdata, 'file').then(()=>{
            ax.post('/post.php', formdata);
        });
    }

    render() {
        return (
            <form method="post" action="post.php" encType="multipart/form-data" onSubmit={this.onSubmit}>
                <div>
                    <InputImage2 />
                </div>
                <div style={{ paddingTop: '1em', textAlign: 'center' }}>
                    <button type="submit">Send</button>
                </div>
            </form>
        );
    }
}
export default App;