import { Button, ButtonGroup, Dialog, Fab, IconButton } from "@mui/material";
import React, { useState, useRef } from "react";

import ClearIcon from '@mui/icons-material/Clear';
import ReplayIcon from '@mui/icons-material/Replay';
import InsertPhotoIcon from '@mui/icons-material/InsertPhoto';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';
import CircleIcon from '@mui/icons-material/Circle';
import CancelIcon from '@mui/icons-material/Cancel';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import CheckIcon from '@mui/icons-material/Check';
import { Box } from "@mui/system";

const env = window.kcapp.env;
const defaultAvatar = env.settings.placeholder_image;

export const prepareFormData = function (form, formData, inputName) {
    return new Promise((resolve, reject) => {
        let hidden = form.querySelector('input[type=hidden]');
        if (hidden == null || hidden.value != 'update') return reject();
    
        let img = form.querySelector('img[data-default]');
        if (img == null) return reject();
    
        let canvas = document.createElement('canvas');
        let c2d = canvas.getContext('2d');
        c2d.drawImage(img, 0, 0, img.width, img.height);
    
        let file = null;
        canvas.toBlob((blob) => {
            file = new File([blob], 'image.jpg', { type: 'image/jpeg' });
            formData.append(inputName, file);
            return resolve();
        }, 'image/jpeg');
    });
};

let init_state = {
    avatar: defaultAvatar,
    imgStyle: {
        display: 'block',
        margin: '0 auto',
        maxWidth: '100%'
    },
    accept: ".jpg, .jpeg, .gif, .png",
    inputName: "file",
    actionName: "file_action",
    actionValue: "none", // values: none, remove, update
    buttonContainerStyle: {
        display: 'flex',
        alignItems: 'center',
        flexDirection: 'column',
        marginTop: '1em'
    },
    buttonGroupProps: {
        variant: "contained",
        color: "inherit",
        size: "large"
    },
    // camera capture
    stream: null,
    camera: false,
    snapshot: null
};
/**
Example:
<InputImage 
    inputName="file"
    actionName="file_action"
    input_size="small"
    avatar="URL"
    accept=".jpg, .jpeg, .gif, .png"
    imgStyle={{}}
/>

props:
    actionName="file_action"
        Optional. Name of the input hidden. Default value is "file_action"
    actionValue="[none|remove|update]"
        Optional. Value of the input hidden. Default value is "none"
    avatar="URL"
        Optional. Image URL. Default value is null
    imgStyle={OBJ} 
        Optional. Object that describes the style for the image. Default value is { display: 'block', margin: '0 auto', maxWidth: '100%' }
    input_size="[small|medium|large]"
        Optional. Size of the buttons. Default value is "large"
    inputName="INPUT_NAME" 
        Optional. Name of the input file. Default value is "file"
    accept=".jpg, .jpeg, .gif, .png"
        Optional. String that describe the file types accepted by the input file. Default value is ".jpg, .jpeg, .gif, .png"
 */
class InputImage2 extends React.Component {
    constructor(props) {
        super(props);

        this.inputRef = React.createRef();

        let newState = {
            ...init_state,
            ...props
        };
        newState.avatarOriginal = newState.avatar;
        if (newState.avatar == null) {
            newState.avatarOriginal = defaultAvatar;
            newState.avatar = defaultAvatar;
        }
        this.state = newState;

        this.player = null;
    }

    selectClick = (evt) => {
        this.inputRef.current.click();
    }

    removeClick = (evt) => {
        this.setState({
            avatar: defaultAvatar,
            actionValue: "remove"
        });
    }

    restoreClick = (evt) => {
        this.setState({
            avatar: this.state.avatarOriginal,
            actionValue: "none"
        });
    }

    captureClick = async (evt) => {
        try {
            let stream = await navigator.mediaDevices.getUserMedia({
                audio: false,
                video: {
                    facingMode: "environment"
                }
            });
            this.setState({
                stream: stream,
                camera: true
            });
        } catch (err) {
            window.alert(err);
        }
    }

    snapshotClick = () => {
        this.pWidth = this.player.videoWidth;
        this.pHeight = this.player.videoHeight;
        this.setState({ camera: 'confirm' });
        let canvas = document.createElement('canvas');
        canvas.width = this.player.videoWidth;
        canvas.height = this.player.videoHeight;
        let context = canvas.getContext('2d');
        context.drawImage(this.player, 0, 0, canvas.width, canvas.height);
        this.setState({ snapshot: canvas.toDataURL('image/jpeg') });
    }

    closeCaptureDialog = () => {
        if (this.player && this.player.srcObject) {
            let tracks = this.player.srcObject.getVideoTracks();
            tracks.forEach((track) => {
                track.stop();
            });
        }
        this.setState({ camera: false, snapshot: null });
    }

    canUseMediaDevice = () => {
        return (navigator.mediaDevices && navigator.mediaDevices.getUserMedia);
    }

    initStream = () => {
        if (this.player && this.state.stream) {
            this.player.srcObject = this.state.stream;
        }
    }

    inputChange = (evt) => {
        if (evt.target.files.length == 0) {
            this.restoreClick();
            return;
        }

        let file = evt.target.files[0];
        let reader = new FileReader();
        let url = reader.readAsDataURL(file);
        reader.onloadend = (e) => {
            this.setState({
                avatar: reader.result,
                actionValue: "update"
            });
        };
    }

    centerImgProp = () => {
        let prop = {};
        const wWidth = window.innerWidth;
        const wHeight = window.innerHeight;
        const wRadio = wWidth / wHeight;

        if (this.player == null) {
            if (wRadio > 1) {
                prop = { width: 'auto', height: '100%' };
            } else {
                prop = { width: '100%', height: 'auto' };
            }
            return prop;
        }

        const pWidth = this.pWidth; //this.player.videoWidth;
        const pHeight = this.pHeight; //this.player.videoHeight;
        const pRadio = pWidth / pHeight;

        if (wRadio > pRadio) {
            prop = { width: 'auto', height: '100%' };
        } else {
            prop = { width: '100%', height: 'auto' };
        }
        return prop;
    }

    render() {
        const st = this.state;
        const props = this.props;
        const sxActionButtons = {
            '& > span.MuiButton-startIcon': {
                mr: 0
            }
        };

        const input_size = props.input_size == undefined ? "large" : props.input_size;

        if (this.player && st.camera && st.stream) {
            this.player.srcObject = st.stream;
        }

        const centerprop = this.centerImgProp();
        let imgStyle = (centerprop.height == 'auto') ?
            {
                margin: 0,
                position: 'absolute',
                top: '50%',
                transform: 'translateY(-50%)'
            }
            :
            {
                margin: '0 auto'
            };
        imgStyle = {
            ...imgStyle,
            display: 'block',
            width: centerprop.width,
            height: centerprop.height
        };

        return (
            <React.Fragment>
                <img alt=""
                    src={st.avatar}
                    data-default={defaultAvatar}
                    data-original={st.avatar}
                    style={st.imgStyle}
                />
                <input
                    ref={this.inputRef}
                    type="file"
                    name={st.inputName}
                    accept={st.accept}
                    data-role="avatar_file"
                    style={{ display: 'none' }}
                    onChange={this.inputChange}
                />
                <input
                    type="hidden"
                    name={st.actionName}
                    value={st.actionValue}
                />
                <div style={st.buttonContainerStyle}>
                    <ButtonGroup {...st.buttonGroupProps}>
                        {
                            this.canUseMediaDevice() ? (
                                <Button
                                    size={input_size}
                                    startIcon={<PhotoCameraIcon />}
                                    sx={sxActionButtons}
                                    onClick={this.captureClick}
                                    title="Capture" />
                            ) : null
                        }

                        <Button
                            size={input_size}
                            startIcon={<InsertPhotoIcon />}
                            sx={sxActionButtons}
                            onClick={this.selectClick}
                            title="Upload" />
                        <Button
                            size={input_size}
                            startIcon={<ReplayIcon />}
                            sx={sxActionButtons}
                            onClick={this.restoreClick}
                            title="Restore"
                        />
                        <Button
                            size={input_size}
                            startIcon={<ClearIcon />}
                            sx={sxActionButtons}
                            onClick={this.removeClick}
                            color="error"
                            title="Remove"
                        />
                    </ButtonGroup>
                </div>

                {
                    <Dialog
                        open={(st.camera === false) ? false : true}
                        onClose={this.closeCaptureDialog}
                        fullScreen
                    >
                        {
                            <React.Fragment>
                                <video
                                    autoPlay
                                    style={{
                                        width: '100%',
                                        height: '100%',
                                        display: (st.snapshot == null) ? 'block' : 'none'
                                    }}
                                    ref={(videoref) => {
                                        this.player = videoref;
                                        this.initStream();
                                    }}
                                />
                                {
                                    (st.snapshot != null) ? (
                                        <div
                                            style={{
                                                position: 'relative',
                                                width: '100%',
                                                height: '100%'
                                            }}
                                        >
                                            <img alt=""
                                                src={st.snapshot}
                                                style={imgStyle} />
                                        </div>
                                    ) : null
                                }
                            </React.Fragment>
                        }
                        <div style={{ width: '100%', position: 'absolute', top: '1em', textAlign: 'right' }}>
                            <IconButton color="primary" size="large" onClick={this.closeCaptureDialog}>
                                <CancelIcon style={{ width: '48px', height: '48px' }} />
                            </IconButton>
                        </div>
                        <div style={{ width: '100%', position: 'absolute', bottom: '1em', textAlign: 'center' }}>
                            {
                                st.camera == 'confirm' ?
                                    (
                                        <Box sx={{ '& > :not(style)': { m: 1 } }}>
                                            <Fab color="success"
                                                onClick={() => {
                                                    this.setState({
                                                        avatar: st.snapshot,
                                                        actionValue: "update"
                                                    });
                                                    this.closeCaptureDialog();
                                                }}>
                                                <CheckIcon fontSize="large" />
                                            </Fab>
                                            <Fab color="error"
                                                onClick={() => {
                                                    this.setState({ camera: true, snapshot: null });
                                                }}>
                                                <HighlightOffIcon fontSize="large" />
                                            </Fab>
                                        </Box>
                                    )
                                    :
                                    (
                                        <IconButton color="primary" onClick={this.snapshotClick}>
                                            <CircleIcon style={{ width: '64px', height: '64px' }} />
                                        </IconButton>
                                    )
                            }
                        </div>

                    </Dialog>
                }
            </React.Fragment>
        );
    }
}

export default InputImage2;