if (window.kcapp === undefined) window.kcapp = {};

(function () {
    const { env, sesto } = window.kcapp;

    const _setAuth = (init = {}) => {
        if (sesto == null) return init;
        init.headers.set('Authorization', 'Bearer ' + sesto.get('jwt'));
        return init;
    }

    function _setJson(body, init = {}) {
        this.is_json = false;

        let content = typeof body == 'string' ? body : JSON.stringify(body);
        init.headers.set('Content-Type', 'application/json');
        return [content, init];
    }

    const _setUrl = (partialUrl) => {
        if (env == null || env.api == null || env.api.base_url == null) {
            let url = new URL(window.location.href);
            return url.origin + url.pathname + partialUrl;
        }
        return env.api.base_url + partialUrl;
    };

    let _setQueryParams = (url, params) => {
        const linearize = (keyname, value, usp) => {
            if (Array.isArray(value)) {
                // process an array
                value.forEach((elem, ind) => {
                    linearize(keyname + '[' + ind + ']', elem, usp);
                });
            } else if (typeof value == 'object') {
                // process an object
                for (let k in value) {
                    linearize(keyname + '[' + k + ']', value[k], usp);
                }
            } else {
                usp.append(keyname, value);
            }
        };

        let u = new URL(url, window.location.origin);
        let u_params = Object.fromEntries(u.searchParams.entries());
        let new_params = {
            ...u_params,
            ...params
        };
        let resp_params = new URLSearchParams();
        for (let k in new_params) {
            linearize(k, new_params[k], resp_params);
        }
        let resp_url = u.origin + u.pathname;
        return (resp_url + '?' + resp_params.toString());
    };

    /**
     * process all request responses
     * @param {Response} response 
     * @param {function} resolve 
     * @param {function} reject 
     */
    const _processResponse = (response, resolve, reject) => {
        const headers = Object.fromEntries(response.headers.entries());

        response.httpHeaders = headers;
        response.text().then(text => {
            response.dataText = text;
            if (headers['content-type'] && headers['content-type'].indexOf('application/json') == 0) {
                response.data = JSON.parse(text);
            } else {
                response.data = {};
            }
            if (response.ok) {
                return resolve(response);
            } else {
                if (response.status == 401) {
                    window.kcapp.func.setJwt(null);
                }
                return reject({ ...response });
            }
            // return response.ok ? resolve(response) : reject(response);
        });
    };

    /**
     * 
     * @param {string} method Method name: 'GET', 'POST', 'PUT', 'DELETE'
     * @param {string} partialUrl i.e. /carpetrollwhs/listall
     * @param {Blob|BufferSource|FormData|String|URLSearchParams} body 
     * @param {Object} urlParams 
     * @param {Object} init 
     * @returns 
     */
    function _request(method, partialUrl, body = null, urlParams = {}, init = {}) {
        init.method = method;
        init.headers = new Headers(init.headers);

        if (body != null) init.body = body;

        if (this.is_json) {
            if (method == 'GET') {
                console.log('GET request can not use .json()');
                throw new Error('GET request can not use .json()');
            }
            [body, init] = _setJson.call(this, body, init);
            init.body = body;
        }

        let req = new Request(_setQueryParams(_setUrl(partialUrl), urlParams), _setAuth(init));
        return new Promise((resolve, reject) => {
            fetch(req)
                .then(resp => {
                    // console.log(Object.fromEntries(resp.headers.entries()));
                    return _processResponse(resp, resolve, reject);
                });
        });
    }

    class Fetchx {
        construct() {
            this.is_json = false;
            this.is_multipart = false;
        }

        json = () => {
            this.is_json = true;
            return this;
        }

        /**
         * Performs a GET request
         * @param {string} partialUrl i.e. /auth/login 
         * @param {object} params 
         * @param {object} init 
         * @returns 
         */
        get = async (partialUrl, urlParams = {}, init = {}) => {
            // _request.call(this, method, partialUrl, body = null, urlParams = {}, init = {}) {
            return _request.call(this, 'GET', partialUrl, null, urlParams, init);
        }
        /**
         * Performs a POST request
         * @param {string} partialUrl i.e. /carpetrollwhs/listall
         * @param {Blob|BufferSource|FormData|String|URLSearchParams} postData 
         * @param {Object} urlParams 
         * @param {Object} init 
         * @returns 
         */
        post = async (partialUrl, postData = null, urlParams = {}, init = {}) => {
            return _request.call(this, 'POST', partialUrl, postData, urlParams, init);
        }
        /**
         * Performs a PUT request
         * @param {string} partialUrl i.e. /carpetrollwhs/listall
         * @param {Blob|BufferSource|FormData|String|URLSearchParams} putData 
         * @param {Object} urlParams 
         * @param {Object} init 
         * @returns 
         */
        put = async (partialUrl, putData = null, urlParams = {}, init = {}) => {
            return _request.call(this, 'PUT', partialUrl, putData, urlParams, init);
        }
        /**
         * Performs a DELETE request
         * @param {string} partialUrl 
         * @param {Object} urlParams 
         * @param {Object} init 
         * @returns 
         */
        delete = async (partialUrl, urlParams = {}, init = {}) => {
            return _request.call(this, 'DELETE', partialUrl, null, urlParams, init);
        }
    }
    window.kcapp.fetchx = new Fetchx();
})();